import styleRegistration from "./Registration.module.scss";
import React from "react";
import InputText from "./inputText/InputText";

const Registration = () => {
  const arr = [
    {
      id: "nickNam",
      value: "Nickname",
      placeholder: ": Explore23",
      type: "text",
    },
    {
      id: "email",
      value: "Email",
      placeholder: ": explore@gmail.com",
      type: "email",
    },
    {
      id: "password",
      value: "Password",
      placeholder: ": 230cd_3rD",
      type: "password",
    },
  ];

  const formElem = arr.map(({ value, placeholder, id, type }) => (
    <InputText
      key={id}
      value={value}
      placehold={placeholder}
      id={id}
      type={type}
    />
  ));
  return (
    <form className={styleRegistration.container}>
      <div>{formElem}</div>
      <button className={styleRegistration.button}></button>
    </form>
  );
};

export default Registration;
